import React, { useEffect, useState } from 'react';
import { useFirebase } from '../../context/FirebaseContext';
import Carousel from 'react-bootstrap/Carousel';
import Button from 'react-bootstrap/Button';
import Spinner from 'react-bootstrap/Spinner';
import { makeStyles } from '@material-ui/core';

const useStyles = makeStyles({
    carousel : {
        width: '85%',
        height: '400px',
        margin: '0px auto 20px auto'
    },
    imagen : {
        width: '300px'
    },
    new : {
        display: 'flex',
        flexFlow: 'column',
        width: '85%',
        padding: '10px',
        border: '1px solid rgb(215, 213, 213)',
        margin: '20px auto 10px auto'
    },
    button: {
        marginTop: '10px'
    }
})

const Pics = () => {
    const firebase = useFirebase();
    const classes = useStyles();

    const [files, setFiles] = useState([]);
    const [images, setImges] = useState([]);
    const [selectedFile, setselectedFile] = useState(null);

    useEffect(async () => {
        firebase.listFiles('recuerdos').then(res => setFiles(res.items))
    }, [])

    useEffect(async () => {
        Promise.all(
            files.map(ref => firebase.downloadFile(ref))
        ).then(list => setImges(list))
    }, [files])

    return (
        <>
            <h4>Fotos</h4>
            <div className={classes.new}>
                <input type='file' onChange={e => setselectedFile(e.target.files[0])} />
                <Button 
                    className={classes.button}
                    onClick={() => firebase.addFile(selectedFile, 'recuerdos', selectedFile.name)}
                    enabled={selectedFile !== true}
                >
                    Subir foto
                </Button>
            </div>
            <Spinner style={{visibility: (images.length === 0) ? 'visible' : 'collapse'}} animation="border" role="status">
                <span className="visually-hidden">Loading...</span>
            </Spinner>
            <Carousel className={classes.carousel}>
                {
                    (images === undefined || images.length === 0) ? null : images.map((image, index) => (
                        <Carousel.Item><img key={index} src={image} className={classes.imagen}/></Carousel.Item> )
                    )
                }
            </Carousel>
        </>
    )
}

export default Pics;